import React, { Fragment, useEffect, useState } from "react";
import { Input, Checkbox, Form, Button, Radio, Spin, notification } from "antd";
import { Link } from "react-router-dom";
import $ from "jquery";
import axiosInstance from "../../../actions/axios";
import { updateTitle } from "../../common/UpdateTitle";
const { TextArea } = Input;

const CreateQuestion = (props) => {
  const [loading, setLoading] = useState(false);
  const [subTopics, setSubTopics] = useState([]);
  const [subTopicID, setSubTopicID] = useState(0);
  const openNotificationWithIcon = (type, mes, des) => {
    notification[type]({
      message: mes,
      description: des,
      duration: 4,
    });
  };

  useEffect(() => {
    axiosInstance
      .get("/set/subtopic/")
      .then((res) => {
        setSubTopics(res.data);
        if (res.data.length !== 0) {
          setSubTopicID(res.data[0].id);
          $(`[data-subtopicid=${res.data[0].id}]`).addClass("table-active");
        }
      })
      .catch((err) => {
        console.log(err);
      });
    updateTitle({
      title: "Create Question",
    });
  }, [setSubTopics]);
  const selectSubTopic = (event) => {
    setSubTopicID(0);
    const id = parseInt(event.currentTarget.getAttribute("data-subtopicid"));
    setSubTopicID(id);

    $("tr.subject-class.table-active").removeClass("table-active");
    event.currentTarget.classList.add("table-active");
  };
  const [question_create] = Form.useForm();

  const cqQuestion = (value) => {
    setLoading(true);
    var answer = [];
    var choice = [];
    for (var a in value.answer) {
      answer.push(value[value.answer[a]]);
    }
    answer = JSON.stringify(answer);
    for (let i = 1; i < 5; i++) {
      choice.push({
        // dummy id that won't effect in backend
        id: i,
        option: value[`option${i}`],
      });
    }
    const questionBody = JSON.stringify({
      label: value.label,
      explanation: value.explanation,
      answer: answer,
      mode: value.mode,
      subtopic: subTopicID,
      choice: choice,
    });
    axiosInstance
      .post("/quiz/create/", questionBody)
      .then((res) => {
        setLoading(false);
        const resBody = (
          <Fragment>
            Question: <h5>{res.data.label}</h5>
            Answer: {answer.toString()}
          </Fragment>
        );
        openNotificationWithIcon("success", "Successfully Added", resBody);
        question_create.resetFields();
      })
      .catch((err) => {
        const errBody = <code>{JSON.stringify(err.response.data)}</code>;
        openNotificationWithIcon("error", "Error occured!!!", errBody);
        setLoading(false);
      });
  };
  return (
    <div className="mdk-header-layout__content page-content pt-32pt pt-sm-64pt pb-32pt">
      <div className="pt-32pt pt-sm-64pt">
        <div className="container page__container d-flex flex-column flex-md-row align-items-center text-center text-sm-left">
          <div className="flex d-flex flex-column flex-sm-row align-items-center">
            <div className="mb-24pt mb-sm-0 mr-sm-24pt">
              <h2 className="mb-0">Creaate Question</h2>

              <ol className="breadcrumb p-0 m-0">
                <li className="breadcrumb-item">
                  <Link to="/">Home</Link>
                </li>

                <li className="breadcrumb-item active">Add Question</li>
              </ol>
            </div>
          </div>
        </div>
      </div>

      <div className="page-section border-bottom-2">
        <div className="container page__container">
          <Spin spinning={loading} size="large">
            <div className="row align-items-start">
              <div className="col-md-8 order-md-first">
                <div className="page-separator">
                  <div className="page-separator__text">New Question</div>
                </div>
                <div className="card card-body">
                  <Form
                    form={question_create}
                    name="question"
                    onFinish={cqQuestion}
                    initialValues={{
                      mode: 0,
                    }}
                  >
                    <div className="form-group">
                      <label className="form-label">Question</label>
                      <Form.Item name="label" noStyle>
                        <Input.TextArea
                          showCount
                          rows="6"
                          placeholder="Question label..."
                          maxLength={1000}
                        />
                      </Form.Item>
                    </div>
                    <Form.Item name="answer" noStyle>
                      <Checkbox.Group style={{ width: "100%" }}>
                        <div className="form-group row">
                          <div className="col">
                            <Checkbox value="option1">
                              <label className="form-label" htmlFor="option1">
                                Optioon 1
                              </label>
                            </Checkbox>
                            <Form.Item name="option1" noStyle>
                              <TextArea id="option1" placeholder="Option 1" />
                            </Form.Item>
                          </div>
                          <div className="col">
                            <Checkbox value="option2">
                              <label className="form-label" htmlFor="option2">
                                Optioon 2
                              </label>
                            </Checkbox>

                            <Form.Item name="option2" noStyle>
                              <TextArea id="option2" placeholder="Option 2" />
                            </Form.Item>
                          </div>
                        </div>

                        <div className="row form-group">
                          <div className="col">
                            <Checkbox value="option3">
                              <label className="form-label" htmlFor="option3">
                                Optioon 3
                              </label>
                            </Checkbox>
                            <Form.Item name="option3" noStyle>
                              <TextArea id="option3" placeholder="Option 3" />
                            </Form.Item>
                          </div>
                          <div className="col">
                            <Checkbox value="option4">
                              <label className="form-label" htmlFor="option4">
                                Optioon 4
                              </label>
                            </Checkbox>

                            <Form.Item name="option4" noStyle>
                              <TextArea id="option4" placeholder="Option 4" />
                            </Form.Item>
                          </div>
                        </div>
                      </Checkbox.Group>
                    </Form.Item>
                    <div className="form-group">
                      <label className="form-label">Explanation</label>
                      <Form.Item name="explanation" noStyle>
                        <Input.TextArea
                          showCount
                          rows="5"
                          placeholder="Answer Explanation"
                          maxLength={1000}
                        />
                      </Form.Item>
                    </div>

                    <div className="form-group">
                      <label className="form-label">Question Mode</label>
                      <Form.Item name="mode">
                        <Radio.Group buttonStyle="solid">
                          <Radio.Button value={0}>Easy</Radio.Button>
                          <Radio.Button value={1}>Medium</Radio.Button>
                          <Radio.Button value={2}>Hard</Radio.Button>
                        </Radio.Group>
                      </Form.Item>
                    </div>

                    <div className="form-group">
                      <Form.Item>
                        <Button
                          type="primary"
                          className="btn btn-primary"
                          htmlType="submit"
                        >
                          Add Question
                        </Button>
                      </Form.Item>
                    </div>
                  </Form>
                </div>
              </div>

              <div className="col-md-4 order-first">
                <div className="page-separator">
                  <div className="page-separator__text">Set SubTopics</div>
                </div>
                <div className="card">
                  <table className="table mb-0 thead-border-top-0 table-nowrap">
                    <tbody
                      className="list"
                      id="search"
                      style={{
                        maxHeight: "350px",
                        overflowY: "scroll",
                        display: "block",
                      }}
                    >
                      {subTopics.map((sub) => (
                        <tr
                          key={sub.id}
                          data-subtopicid={sub.id}
                          className="subject-class"
                          onClick={selectSubTopic}
                        >
                          <td>
                            <div className="d-flex flex-column">
                              <p className="mb-0">
                                <strong className="js-lists-values-name">
                                  {sub.name}
                                </strong>
                              </p>
                            </div>
                          </td>
                        </tr>
                      ))}
                      {subTopics.length ? null : (
                        <tr>
                          <td>
                            <div className="d-flex flex-column">
                              <p className="mb-0">
                                <strong className="js-lists-values-name">
                                  Empty
                                </strong>
                              </p>
                            </div>
                          </td>
                        </tr>
                      )}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </Spin>
        </div>
      </div>
    </div>
  );
};

export default CreateQuestion;
